import React from 'react'
import { Dialog } from 'primereact/dialog'

export default function PrimeDialog({ isOpen, setIsOpen, children, withs, setFooter, header }) {
    return (
        <Dialog
            header={header}
            visible={isOpen}
            style={withs ? { width: withs + 'rem' } : { width: '30rem' }}
            onHide={() => setIsOpen(false)}
            modal>
            {children}
        </Dialog>
    )
}
