import {
    addBreadcrumbType,
    breadcrumbType,
    pageTitleType,
    gridListType,
    barListType
} from "../action_types"

const reducer = (state = { breadcrumbs: [], bar: true, grid: false }, action) => {
    switch (action.type) {
        case pageTitleType:
            return { ...state, title: action.payload }
            break;
        case breadcrumbType:
            return { ...state, breadcrumbs: action.payload }
            break;
        case gridListType:
            return { ...state, grid: true, bar: false }
            break;
        case barListType:
            return { ...state, bar: true, grid: false }
            break;
        default:
            return state
            break;
    }
}

export default reducer