import React from 'react'
import { Button } from 'primereact/button'

export default function PrimeButton({ content, icon, iconPos, classes, disabled, sizes, onClick }) {
    return (
        <Button 
            label={content}
            icon={icon}
            iconPos={iconPos}
            className={`${classes ? 'p-button ' + classes : 'p-button-secondary'} ${sizes ? 'p-button-' + sizes : ''}`}
            disabled={disabled}
            onClick={onClick} />
    )
}
