import React, { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import LeftMenu from "./LeftMenu";
import Navbar from "./Navbar";
import Content from "./Content";
import { Button } from "primereact/button"
import { TurkishIcon, RussiaIcon, UnitedStateIcon, ThemeIcon } from '../icons'

function Layout(props) {
    const dispatch = useDispatch();
    const trans = useSelector(state => state.trans)
    const { children } = props;
    const [isOpen, setIsOpen] = useState(false)

    const changeLang = (lng) => {
        dispatch({ type: lng });
        localStorage.setItem("lang", lng);
    }

    return (
        <div className="layout">
            <Navbar />
            <LeftMenu />
            <Content children={children} />
            <div className={`${isOpen ? 'layout-config layout-config-active' : 'layout-config'}`}>
                <div className="layout-config-content-wrapper">
                    <button className="layout-config-button p-link" onClick={() => setIsOpen(!isOpen)}>
                        <i className='pi pi-cog'></i>
                    </button>
                    <button className="layout-config-close p-link" onClick={() => setIsOpen(!isOpen)}>
                        <i className='pi pi-times'></i>
                    </button>
                    <div className="layout-config-content">
                        <div>
                            <h4 className='mb-2'>Theme</h4>
                            <div className="p-grid mb-3">
                                <div className="col-3">
                                    <Button className="p-button-outlined p-button-secondary" tooltip='Light'>
                                        <ThemeIcon />
                                    </Button>
                                </div>
                                <div className="col-3">
                                    <Button className="p-button-outlined p-button-secondary" tooltip='Dark'>
                                        <ThemeIcon />
                                    </Button>
                                </div>
                                <div className="col-3">
                                    <Button className="p-button-outlined p-button-secondary" tooltip='Blue'>
                                        <ThemeIcon color='#20262e'/>
                                    </Button>
                                </div>
                            </div>
                            <h4 className='mb-2'>Language</h4>
                            <div className="p-grid mb-3">
                                <div className="col-3">
                                    <button className="cursor-pointer" title='Turkish' onClick={() => changeLang('tr')}>
                                        <TurkishIcon />
                                    </button>
                                </div>
                                <div className="col-3">
                                    <button className="cursor-pointer" title='Russian' onClick={() => changeLang('ru')}>
                                        <RussiaIcon />
                                    </button>
                                </div>
                                <div className="col-3">
                                    <button className="cursor-pointer" title='English' onClick={() => changeLang('en')}>
                                        <UnitedStateIcon />
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Layout
