import React, { useState, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import Layout from "../components/Container/Layout"
import { Card } from 'primereact/card'
import ContentLoader from "react-content-loader"
import PrimeInputText from '../components/UIElement/PrimeInputText'
import PrimeDialog from '../components/UIElement/PrimeDialog'
import PrimeButton from '../components/UIElement/PrimeButton'
import { fetchCustomerType } from '../redux/actions/customer_type_ac'
import { crud } from '../config/contants'
import { EditIcon, TrashIcon, ListIcon, GridIcon, SortIcon, LeftIcon, RightIcon } from '../components/icons'
import { barListType, gridListType, customerTypes } from '../redux/action_types'
import { success } from "../redux/actions/loader_ac"
import { Button, Input, Modal } from 'antd';
import { SearchOutlined, PlusOutlined } from '@ant-design/icons';
import Operation from 'antd/lib/transfer/operation'

export default function Home() {
    const dispatch = useDispatch();
    const current = useSelector(state => state.current)
    const theme = useSelector(state => state.theme)
    const loader = useSelector(state => state.loader)
    const result = useSelector(state => state.result)
    const trans = useSelector(state => state.trans)
    const customerType = useSelector(state => state.customerType)
    const [openDialog, setOpenDialog] = useState(false)
    const [isEdit, setIsEdit] = useState(false)
    const [isDelete, setIsDelete] = useState(false)
    const [item, setItem] = useState({})
    const [request, setRequest] = useState({ rowCount: 9, pageCount: 0, load: true, search: "", sort: "id", "desc": false, data: {} })

    useEffect(() => {
        //dispatch(fetchCustomerType(request, crud.list, {}))
    }, [])

    useEffect(() => {
        if (result.success === true) {
            setOpenDialog(false)
            dispatch(success(false))
        }
    }, [result.change])

    var fields = {
        id: 0,
        name: ''
    }
    const [field, setField] = useState(fields)

    var errors = {
        errName: ''
    }
    const [error, setError] = useState(errors)

    let validate = () => {
        if (field.name.length < 5) {
            setError({ ...error, errName: 'Kayıt çok kısa' });
            return false;
        }
        if (field.name.length > 30) {
            setError({ ...error, errName: 'Kayıt çok uzun' });
            return false;
        }

        return true;
    }

    const showGrid = () => {
        dispatch({ type: gridListType })
    }

    const showBar = () => {
        dispatch({ type: barListType })
    }

    const register = () => {
        if (validate()) {
            if (!isEdit)
                dispatch(fetchCustomerType({}, crud.create, field));
            else {
                dispatch(fetchCustomerType({}, crud.update, field));
            }
            setOpenDialog(false);
        }
    }

    const editItem = (e) => {
        setIsEdit(true);
        setOpenDialog(true);
        setField(e);
    }

    const deleteComfirm = (e) => {
        setIsDelete(true);
        setItem(e);
    }

    const deleteItem = () => {
        setIsDelete(false)
        dispatch(fetchCustomerType(request, crud.delete, item))
        let customers = customerType.list.filter((e) => {
            return e.id !== item.id
        })
        dispatch({ type: customerTypes, payload: customers });
    }

    const seacrhItem = (text) => {
        if (text.length > 0)
            dispatch(fetchCustomerType({ ...request, search: text, load: false }, crud.list))
        else
            dispatch(fetchCustomerType({ ...request, load: false }, crud.list))
    }

    const [desc, setDesc] = useState(false)
    const sortByField = (f) => {
        setRequest({ ...request, sort: f, desc })
        dispatch(fetchCustomerType({ ...request, sort: f, desc, load: false }, crud.list));
        setDesc(!desc)
    }

    return (
        <Layout>
            {/* <PrimeDialog
                isOpen={openDialog}
                setIsOpen={setOpenDialog}
                header={trans.customerType}
                withs='30'>
                <div className='row'>
                    <div className='col-12'>
                        <PrimeInputText
                            header={trans.name}
                            placeholder={trans.name}
                            value={field.name}
                            message={error.errName}
                            onChange={(e) => setField({ ...field, name: e.target.value })} />
                        <PrimeButton
                            classes='p-button-success p-button-raised p-button-text'
                            content={trans.save}
                            onClick={register} />
                    </div>
                </div>
            </PrimeDialog> */}
            <PrimeDialog
                isOpen={isDelete}
                setIsOpen={setIsDelete}
                header={trans.warning}
                withs='20'>
                <div className='row'>
                    <div className='col-12 mb-3'>
                        <span className='warning_message'>
                            {trans.doYouWantToDelete}
                        </span>
                    </div>
                    <div className='col-12'>
                        <PrimeButton
                            classes='p-button-danger p-button-raised p-button-text'
                            content={trans.yes}
                            onClick={deleteItem} />
                    </div>
                </div>
            </PrimeDialog>
            <Modal
                title={trans.customerType}
                visible={openDialog}
                okText={trans.save}
                cancelText={trans.cancel}
                onOk={register}
                onCancel={() => setOpenDialog(false)}>
                <div className='input-group'>
                    <span className='title'>{trans.name}</span>
                    <Input
                        value={field.name}
                        onChange={e => setField({ ...field, name: e.target.value })}
                        placeholder={trans.name} />
                    <span className='messages'>{error.errName}</span>
                </div>
            </Modal>
            <div className="home">
                <Card>
                    <div className='properties'>
                        <div className='left'></div>
                        <div className='right'>
                            <div className='input-group single'>
                                <Input
                                    prefix={<SearchOutlined />}
                                    placeholder={trans.search}
                                    onChange={e => seacrhItem(e.target.value)} />
                            </div>
                            <Button
                                icon={<PlusOutlined />}
                                type='primary'
                                onClick={() => setOpenDialog(true)}>
                                {trans.add}
                            </Button>

                            <div className='view_button'>
                                <button onClick={showBar}>
                                    <ListIcon color={current.bar
                                        ? (theme === "dark" ? "rgba(96, 168, 221, 1)" : "rgba(96, 168, 221, 1)")
                                        : (theme === "dark" ? "#e5e5e5" : "#5d5d5d")} />
                                </button>
                                <button onClick={showGrid}>
                                    <GridIcon color={current.grid
                                        ? (theme === "dark" ? "rgba(96, 168, 221, 1)" : "rgba(96, 168, 221, 1)")
                                        : (theme === "dark" ? "#e5e5e5" : "#5d5d5d")} />
                                </button>
                            </div>
                        </div>
                    </div>
                    {current.bar
                        && (!loader.loading ?
                            <div className='table-responsive'>
                                <table className='table'>
                                    <thead>
                                        <tr>
                                            <th onClick={() => sortByField("name")}>{trans.name} <SortIcon color={theme === 'dark' ? '#f19ea6' : '#212529'} /></th>
                                            <th>{trans.settings}</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {customerType && customerType.list && customerType.list.length > 0 && customerType.list.map((e, i) => {
                                            return (
                                                <tr key={i}>
                                                    <td>{e.name}</td>
                                                    <td>
                                                        <button className='setting' onClick={() => editItem(e)}>
                                                            <EditIcon color={"#8dd0ff"} />
                                                        </button>
                                                        <button className='setting' onClick={() => deleteComfirm(e)}>
                                                            <TrashIcon color={'#f19ea6'} />
                                                        </button>
                                                    </td>
                                                </tr>
                                            )
                                        })}
                                    </tbody>
                                </table>
                                <div className="pagination">
                                    <ul>
                                        <li><LeftIcon color={theme === 'dark' ? '#e5e5e5' : '#3f3f3f'} /></li>
                                        <li className='active'>1</li>
                                        <li>2</li>
                                        <li>3</li>
                                        <li><RightIcon color={theme === 'dark' ? '#e5e5e5' : '#3f3f3f'} /></li>
                                    </ul>
                                </div>
                            </div>
                            :
                            <div className='table-responsive'>
                                <table className='table'>
                                    <thead>
                                        <tr>
                                            <th>{trans.name} <SortIcon color={theme === 'dark' ? '#f19ea6' : '#212529'} /></th>
                                            <th>{trans.settings}</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr className='loader'>
                                            <td className='loader' colSpan='2'>
                                                <ContentLoader
                                                    speed={2}
                                                    width={"100%"}
                                                    height={150}
                                                    viewBox="0 0 1250 150"
                                                    backgroundColor="#cccccc"
                                                    foregroundColor="#636363">
                                                    <rect x="25" y="15" rx="5" ry="5" width="1200" height="10" />
                                                    <rect x="25" y="45" rx="5" ry="5" width="1200" height="10" />
                                                    <rect x="25" y="75" rx="5" ry="5" width="1200" height="10" />
                                                    <rect x="25" y="105" rx="5" ry="5" width="1200" height="10" />
                                                </ContentLoader>
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                        )
                    }
                </Card>
            </div>
        </Layout >
    )
}
